<template>
  <div class="page-checklist">
    <div class="page-title">Checklist</div>

    <mkd-checklist
      class="page-part"
      title=""
      v-model="value1"
      :options="options1">
    </mkd-checklist>

    <div>
      <mkd-cell title="">{{ value1 }}</mkd-cell>
    </div>

    <mkd-checklist
      class="page-part"
      title=""
      v-model="value2"
      :options="options2">
    </mkd-checklist>

    <div>
      <mkd-cell title="">{{ value2 }}</mkd-cell>
    </div>

    <mkd-checklist
      class="page-part"
      title=""
      :max="2"
      v-model="value3"
      :options="options3">
    </mkd-checklist>

    <div>
      <mkd-cell title="">{{ value3 }}</mkd-cell>
    </div>

    <mkd-checklist
      align="right"
      class="page-part"
      title=""
      v-model="value4"
      :options="options4">
    </mkd-checklist>
  </div>
</template>

<script>
export default {
  name: 'page-checklist',

  data() {
    return {
      value1: [],
      value2: [''],
      value3: ['A'],
      value4: []
    };
  },

  created() {
    this.options1 = ['A', 'B', 'C'];
    this.options3 = ['A', 'B', 'C', 'D'];
    this.options4 = ['A', 'B', 'C', 'D'];
    this.options2 = [
      {
        label: '',
        value: 'F',
        disabled: true
      },
      {
        label: '',
        value: '',
        disabled: true
      },
      {
        label: 'A',
        value: 'A'
      },
      {
        label: 'B',
        value: 'B'
      }
    ];
  }
};
</script>

<style lang="scss">
 .page-checklist {
   >*{
     margin-bottom: 10px;
   }
   .page-part {
     margin-top: 40px;
   }
 }
</style>



