<template>
  <div class="page-radio">
    <div class="page-title">radio</div>

    <mkd-radio
      class="page-part"
      title=""
      v-model="value1"
      :options="options1" />
    <div>
      <mkd-cell title="">{{ value1 }}</mkd-cell>
    </div>

    <mkd-radio
      class="page-part"
      title=""
      v-model="value2"
      :options="options2" />
    <div>
      <mkd-cell title="">{{ value2 }}</mkd-cell>
    </div>

    <mkd-radio
      align="right"
      class="page-part"
      title=""
      v-model="value3"
      :options="options3" />
      <div>
        <mkd-cell title="">{{ value3 }}</mkd-cell>
      </div>
    <mkd-radio
      align="right"
      class="page-part"
      title=""
      v-model="value4"
      :options="options4" />
  </div>
</template>

<script>
export default {
  name: 'page-radio',

  data() {
    return {
      value1: '',
      value2: 'A',
      value3: '',
      value4: 'F',
      options1: ['A', 'B', 'C'],
      options2: [
        {
          label: '',
          value: 'F',
          disabled: true
        },
        {
          label: 'A',
          value: 'A'
        },
        {
          label: 'B',
          value: 'B'
        }
      ],
      options3: ['A', 'B', 'C'],
      options4: [
        {
          label: '',
          value: 'F',
          disabled: true
        }
      ]
    };
  }
};
</script>

<style lang="scss">
.page-radio {
  > *{
    margin-bottom: 10px;
  }
}
.page-radio .page-part {
  margin-top: 40px;
}
</style>



