<template>
  <div class="page-button">
    <h1 class="page-title">Button</h1>
    <div class="page-title">1.button</div>
    <div class="page-button-group">
      <mkd-button size="large" @click="openAlert('')" type="primary"></mkd-button>
      <mkd-button size="large" @click="openAlert('')" type="secondary"></mkd-button>
      <mkd-button size="large" @click="openAlert('')" type="danger"></mkd-button>
    </div>

    <div class="page-button-group">
      <mkd-button type="primary"></mkd-button>
      <mkd-button type="secondary"></mkd-button>
      <mkd-button type="danger"></mkd-button>
    </div>

    <div class="page-button-group">
      <mkd-button size="small" type="primary"></mkd-button>
      <mkd-button size="small" type="secondary"></mkd-button>
      <mkd-button size="small" type="danger"></mkd-button>
