      <mkd-button size="large" type="danger">
        <img src="../assets/100x100.png" height="20" width="20" slot="icon">
        
      </mkd-button>
    </div>
    <div class="page-title">2.button</div>
    <div class="page-button-group">
      <mkd-button size="large" type="primary-b"></mkd-button>
      <mkd-button size="large" type="secondary-b"></mkd-button>
      <mkd-button size="large" type="danger-b"></mkd-button>
    </div>

    <div class="page-button-group">
      <mkd-button type="primary-b" @click="openAlert('')"></mkd-button>
      <mkd-button type="secondary-b"></mkd-button>
      <mkd-button type="danger-b"></mkd-button>
    </div>

