<script>
import { MessageBox } from '$src/index'
export default {
  name: 'button',
  methods: {
    openAlert(title) {
      MessageBox.alert(title + '!', 'Button')
    }
  }
};
</script>

<style lang="scss">
.page-button {
  width: 100%;
  text-align: center;
  >.page-button-group {
    margin: 15px auto;
    text-align: center;
    >* {
      margin: 15px auto;
    }
  }
  padding-bottom: 300px;
}
</style>



