<template>
  <div class="page-toast">
    <h1 class="page-title">Toast</h1>
    <div class="page-toast-wrapper">
      <mkd-button @click.native="openToast" size="large"> Toast</mkd-button>
      <mkd-button @click.native="openToastWithIcon" size="large"> icon  Toast</mkd-button>
      <mkd-button @click.native="openBottomToast" size="large"> Toast </mkd-button>
    </div>
  </div>
</template>

<style lang="scss">
.page-toast {
  >.page-toast-wrapper{
    padding: 0 20px;
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
    >:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}
</style>

<script type="text/babel">
  import { Toast } from '$src/index';

  export default {
    methods: {
      openToast() {
        Toast('');
      },

      openToastWithIcon() {
        Toast({
          message: '',
          icon : true
        });
      },

      openBottomToast() {
        Toast({
          message: '',
          position: 'bottom'
        });
      }
    }
  };
</script>



