<template>
  <div class="page-msgbox">
    <h1 class="page-title">Message Box</h1>
    <div class="page-msgbox-wrapper">
      <mkd-button @click.native="openAlert" size="large"> alert </mkd-button>
      <mkd-button @click.native="openConfirm" size="large"> confirm </mkd-button>
      <mkd-button @click.native="openPrompt" size="large"> prompt </mkd-button>
    </div>
  </div>
</template>

<style lang="scss">
.page-msgbox {
  >.page-msgbox-wrapper {
    padding: 0 20px;
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
    >:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}
</style>

<script type="text/babel">
  import { MessageBox } from '$src/index'

  export default {
    methods: {
      openAlert() {
        MessageBox.alert('!', '')
      },
      openConfirm() {
        MessageBox.confirm('?', '').then(({action}) => {
          if (action === "confirm") {
              MessageBox.alert('', '')
          } else if  (action === "cancel") {
              MessageBox.alert('', '')
          }
        })
      },
      openPrompt() {
        MessageBox.prompt(' ', '').then(({action, value}) => {
          if (action === 'confirm' && value) {
            MessageBox.alert(` ${ value }`, '')
          } else if (action === 'cancel') {
            MessageBox.alert('', '')
          }
        })
      }
    }
  }
</script>



